"use client";
import { useEffect, useState, useRef } from "react";
import { useTranslation } from "react-i18next";
import Link from "next/link";
import Image from "next/image";

import LanguageSelector from "@/components/LanguageSelector"
import LanguageSelectorMobile from "@/components/LanguageSelectorMobile";

import logo from "@/public/logo.png";
import profile from "@/public/prof.svg";
import wallet from "@/public/wallet.svg";
import dollar from "@/public/dollar.svg";
import arrow from "@/public/arrow-down.png";

const TheHeader = () => {
  const { t } = useTranslation();

  const [user, setUser] = useState([]);
  const [newUrl, setNewUrl] = useState("");
  // const urlParams = new URLSearchParams(window.location.search);
  const urlParams = new URLSearchParams(
    typeof window !== "undefined" ? window.location.search : ""
  );
  const [showAdditionalOptions, setShowAdditionalOptions] = useState(false);
  const [ipDataCode, setIpDataCode] = useState(null);
  const [isMobile, setIsMobile] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const menuContainerRef = useRef(null);

  const handleMenuToggle = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  useEffect(() => {
    const handleClickOutsideMenu = (event) => {
      if (
        menuContainerRef.current &&
        !menuContainerRef.current.contains(event.target) &&
        !event.target.classList.contains("btn-menu")
      ) {
        setIsMenuOpen(false);
      }
    };

    // –î–æ–±–∞–≤–ª—è–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–∞ –ø—Ä–∏ –º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
    document.addEventListener("click", handleClickOutsideMenu);

    // –û—á–∏—â–∞–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –ø—Ä–∏ —Ä–∞–∑–º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
    return () => {
      document.removeEventListener("click", handleClickOutsideMenu);
    };
  }, [menuContainerRef]);

  useEffect(() => {
    // –î–æ–±–∞–≤—å—Ç–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –¥–ª—è –∏–∑–º–µ–Ω–µ–Ω–∏—è —Ä–∞–∑–º–µ—Ä–∞ –æ–∫–Ω–∞
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 767);
    };

    // –í—ã–∑–æ–≤–∏—Ç–µ —Ñ—É–Ω–∫—Ü–∏—é –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∞ –ø—Ä–∏ –º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
    handleResize();

    // –î–æ–±–∞–≤—å—Ç–µ —Å–ª—É—à–∞—Ç–µ–ª—å —Å–æ–±—ã—Ç–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è —Ä–∞–∑–º–µ—Ä–∞ –æ–∫–Ω–∞
    window.addEventListener("resize", handleResize);

    // –û—á–∏—Å—Ç–∏—Ç–µ —Å–ª—É—à–∞—Ç–µ–ª—å —Å–æ–±—ã—Ç–∏—è –ø—Ä–∏ —Ä–∞–∑–º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  const menuRef = useRef(null);
  const api = "https://pickbonus.myawardwallet.com/api";
  useEffect(() => {
    const idUserParam = urlParams.get("keyword");

    const fetchUsers = async () => {
      try {
        const res = await fetch(`${api}/user/read_one.php?id=${idUserParam}`);
        if (res.ok) {
          const users = await res.json();

          setUser(users);
        } else {
          console.error("Failed to fetch data:", res.status);
        }
      } catch (error) {
        console.error("An error occurred:", error);
      }
    };

    fetchUsers();
  }, []);




  const handleBalanceChange = (event) => {
    event.stopPropagation();
    setShowAdditionalOptions(!showAdditionalOptions);
    // setShowAdditionalOptions((prevState) => !prevState);
  };

  useEffect(() => {
    const url = window.location.href;
    const urlObj = new URL(url);
    const searchParams = new URLSearchParams(urlObj.search);
    const newUrl = "?" + searchParams.toString();
    setNewUrl(newUrl);
  }, [newUrl]);
  useEffect(() => {
    const handleDocumentClick = (event) => {
      event.stopPropagation();
      if (!menuRef.current || menuRef.current.contains(event.target)) {
        // –ï—Å–ª–∏ –∫–ª–∏–∫ –±—ã–ª –≤–Ω—É—Ç—Ä–∏ –º–µ–Ω—é, –Ω–µ —Å–∫—Ä—ã–≤–∞—Ç—å –µ–≥–æ
        return;
      }
      // –ï—Å–ª–∏ –∫–ª–∏–∫ –±—ã–ª –≤–Ω–µ –º–µ–Ω—é, —Å–∫—Ä—ã—Ç—å –º–µ–Ω—é
      setShowAdditionalOptions(false);
    };

    // –î–æ–±–∞–≤—å—Ç–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –ø—Ä–∏ –º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
    document.addEventListener("click", handleDocumentClick);

    // –£–¥–∞–ª–∏—Ç–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –ø—Ä–∏ —Ä–∞–∑–º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
    return () => {
      document.removeEventListener("click", handleDocumentClick);
    };
  }, [handleBalanceChange]);

  console.log("__________", showAdditionalOptions);

  return (
    <header>
      <div className="container">
        <Link className="flex" href={`/${newUrl}`}>
          <Image src={logo} alt={logo} width={175} height={50} loading="lazy" />
        </Link>
        {!isMobile ? (
          <div className="flex ml-auto items-center">
            {Object.keys(user).length > 0 && (
              <div className="tickets">
                <Link href={`/fortunewheel/${newUrl}`}>
                  <Image
                    src={dollar}
                    alt={dollar}
                    width={26}
                    height={26}
                    loading="lazy"
                  />
                  {t("Wheel of Fortune")} <span>{user.tickets}</span>
                </Link>
              </div>
            )}
            <div className="relative">
              {Object.keys(user).length > 0 && (
                <div className="parent">
                  <div
                    className="option flex items-center"
                    value={user.balance}
                    onClick={handleBalanceChange}
                  >
                    {t("Balance")}: {user.balance} USD
                    <Image
                      src={arrow}
                      alt={arrow}
                      width={15}
                      height={15}
                      loading="lazy"
                      className="ml-1"
                    />
                  </div>
                </div>
              )}
              {showAdditionalOptions && (
                <div className="additional-options" ref={menuRef}>
                  <div className="option">
                    <Link
                      className="flex items-center"
                      href={`/withdrawal/${newUrl}`}
                      onClick={() => setShowAdditionalOptions(false)}
                    >
                      <Image
                        src={wallet}
                        alt={wallet}
                        width={25}
                        height={25}
                        loading="lazy"
                        className="mr-1"
                      />
                      {t("Withdraw")}
                    </Link>
                  </div>
                </div>
              )}
            </div>
            <LanguageSelector ipDataCode={ipDataCode} />
          </div>
        ) : (
          <div className="flex ml-auto items-center">
            <div className="mobile-menu">
              {Object.keys(user).length > 0 && (
                <div className="flex items-center">
                  <Link
                    href={`/withdrawal/${newUrl}`}
                    className="wallet flex items-center"
                  >
                    <img src={`.${wallet}`} alt={wallet} />
                    <p>{user.balance} USD</p>
                  </Link>
                  <div
                    className="btn-menu"
                    onClick={handleMenuToggle}
                    ref={menuContainerRef}
                  >
                    <img src={`.${profile}`} alt={profile} />
                    <strong className="ticketspoint">{user.tickets}</strong>
                  </div>
                </div>
              )}
              {isMenuOpen && (
                <div className="list-menu">
                  {Object.keys(user).length > 0 && ( // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ –¥–∞–Ω–Ω—ã–µ –≤ user
                    <Link
                      href={`/withdrawal/${newUrl}`}
                      className="balanceWithdraw"
                    >
                      <img className="mr-1" src={`.${wallet}`} alt={wallet} />
                      {t("Withdraw")} <span>{user.balance} USD</span>
                    </Link>
                  )}
                  <div className="mobile-menu-content">
                    {Object.keys(user).length > 0 && (
                      <Link
                        href={`/fortunewheel/${newUrl}`}
                        className="balanceWithdraw"
                      >
                        <img className="mr-1" src={`.${dollar}`} alt={dollar} />
                        {t("Wheel of Fortune")} <span>{user.tickets}</span>
                      </Link>
                    )}
                    {/* –î—Ä—É–≥–∏–µ —ç–ª–µ–º–µ–Ω—Ç—ã –º–µ–Ω—é –¥–ª—è –º–æ–±–∏–ª—å–Ω–æ–≥–æ –≤–∏–¥–∞ */}
                    {/* ... */}
                  </div>
                </div>
              )}
            </div>
            <LanguageSelectorMobile ipDataCode={ipDataCode} />
          </div>
        )}
      </div>
    </header>
  );
};

export { TheHeader };
